.select-simple {
  padding: 10px 0px 10px 0px;
  margin: 0px 10px 0 0;
  background: #ffffff;
  color: #000000;
  border: 1px solid #555;
  border-radius: 3px;
  background: #ffffff url("http://www.scottgood.com/jsg/blog.nsf/images/arrowdown.gif") no-repeat 95% 50%;
}
.select-simple select {
  cursor: pointer;
  padding: 10px 15px;
  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  text-transform: uppercase;
  font-size: 12px;
}
.select-simple select:focus {
  outline: none;
}
